import {ZporeDropCreated} from 'decentralized/EthereumEvents.js';
import BackendQuery from 'decentralized/BackendQuery.js';
import {STEMS_CONTRACT} from 'stems/contract.js';
import {getSongs} from 'stems/getSongs.js';

let cache = {};
export const getDropAddress = (songId) => {
    if (cache[songId]) {
        return cache[songId];
    }
    let promise = new Promise(async (resolve) => {
        let songs = await getSongs();
        let query = new BackendQuery(STEMS_CONTRACT)
            .from(ZporeDropCreated)
            .select("dropAddress")
            .select("songId");

        if (songId !== undefined) {
            query = query.where("songId", parseInt(songId));
        }
        let results = await query.executeQuery();
        if (songId  !== undefined) {
            resolve(results[0].dropAddress);
        } else {
            results.forEach(
                s => s.song = songs.find(x => x.songId === s.songId));
            resolve(results);
        }
    });
    cache[songId] = promise;
    return promise;
}
