import React, { useEffect, useCallback, useState} from 'react';
import Playhead from './Playhead.js';
import {connect} from 'react-redux';
import PlayButton from 'components/buttons/PlayButton.js';
import {useWaveformPlayer} from './useWaveformPlayer.js';

const PlayButtonAndPlayhead = ({width, buffer, start, length}) => { 
    const {
        startRef,
        lengthRef,
        playhead,
        stop,
        play,
        isPlaying,
        setIsPlaying,
        player
    } = useWaveformPlayer({buffer, start, length});

    useEffect(() => {
        return () => stop();
    }, []);

    return [
          <div className="control-btn-container">
            <PlayButton isPlaying={isPlaying} play={play}/>
          </div>,
        <Playhead
          start={start}
          length={length}
          width={width}
          startRef={startRef}
          lengthRef={lengthRef}
          isPlaying={isPlaying}
          setIsPlaying={setIsPlaying} player={player}/>
    ];
};

const mapStateToProps = (state, props) => {
  return {
      start: state.spore.slicePoints.start,
      length: state.spore.slicePoints.length,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlayButtonAndPlayhead);
