import StemsContract from "decentralized/contracts/StemsContract.js";
import BackendQuery from "decentralized/BackendQuery.js";
import {
    NewSong,
    NewStem,
    NewDefaultSongConfiguration,
    SongConfigurationStem,
    NewStemType} from 'decentralized/EthereumEvents.js';
let contract = new StemsContract().contractAddress;

let cache = {};
export const getSongs = (songIds) => {
    let key = songIds === undefined ? -1 : songIds;
    if (cache[key]) {
        return cache[key];
    }
    let promise = new Promise(async (resolve) => {
        console.log("AB getSongs called with=", songIds);
        let query = new BackendQuery(contract)
            .from(NewSong);
        
        let songs = await query.executeQuery();
        /*
        let stemQuery = new BackendQuery(contract)
            .from(NewStem);
        let stems = await stemQuery.executeQuery();
        */
        
        let _songs = [];
        for (let song of songs) {
            if (_songs.some(x => x.songId === song.songId)) {
                continue;
            }
            _songs.push(song);
        }
        resolve(_songs);
    });
    cache[key] = promise;
    return promise;
}

    
