import React, { useEffect, useRef, useCallback, useState} from 'react';
import ShaderSquare from './ShaderSquare.js';
import Zorb from './Zorb.js';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';
import './animation.scss';
import {color} from './RemixDNADetails.js';

const width = 130;
const height = 130;

const convert = ({x,y}) => {
    x*=0.8;
    y*=0.8;
    return {
        x: width/2 + x*width,
        y: height - (height/2+ y*height)
    };
}

const RemixAnimation = ({remix, audioPlayer, animationData, isPlaying, duration}) => { 
    let startTime = useRef();
    let [coord, setCoord] = useState({x: width/2, y:height/2});
    let [drums, setDrums] = useState();
    let [synths, setSynths] = useState();
    let [drumOptions, setDrumOptions] = useState([]);
    let [synthOptions, setSynthOptions] = useState([]);

    useEffect(() => {
        if (animationData.synths) {
            let _stemIds = Array.from(new Set(animationData.synths.map(x => x.stemId).filter(x => x)));
            for (let i=_stemIds.length; i < 3; i++) {
                _stemIds.push(-1);
            }
            setSynthOptions(_stemIds);
            _stemIds = Array.from(new Set(animationData.drums.map(x => x.stemId).filter(x => x)));
            for (let i=_stemIds.length; i < 3; i++) {
                _stemIds.push(-1);
            }
            setDrumOptions(_stemIds);
        } else {
            setDrumOptions([-1, -1, -1])
            setSynthOptions([-1, -1, -1])
        }
    }, [animationData, setDrumOptions, setSynthOptions]);

    useEffect(() => {
        if (isPlaying) {
            startTime.current = new Date().getTime();
        }
    }, [isPlaying]);

    const onTick = useCallback(() => {
        if (!isPlaying) {
            return;
        }
        let timeElapsed = (new Date().getTime() - startTime.current);;
        timeElapsed /= 1000;
        timeElapsed = timeElapsed % duration;
        let last = undefined;
        let i=0;
        let lastMovement;
        let movements = animationData;
        let synths = [];
        let drums = [];
        if (animationData.movements) {
            movements = animationData.movements;
            drums = animationData.drums;
            synths = animationData.synths;
        }
        for (let {time, movement} of movements) {
            if (timeElapsed <= time) {
                setCoord(convert(lastMovement || movement));
                break;
            }
            lastMovement = movement;
            last = time;
        }
        let lastStemId = undefined;
        for (let {time, stemId} of drums) {
            if (timeElapsed <= time) {
                setDrums(lastStemId === undefined ? stemId : lastStemId);
                break;
            }
            lastStemId = stemId === undefined ? null : stemId;
            last = time;
        }

        lastStemId = undefined;
        for (let {time, stemId} of synths) {
            if (timeElapsed <= time) {
                setSynths(lastStemId === undefined ? stemId : lastStemId);
                break;
            }
            lastStemId = stemId === undefined ? null : stemId;
            last = time;
        }
    }, [setCoord, isPlaying, animationData, duration, setSynths, setDrums]);

    useInterval(onTick, 50);
    
    let zorbId = remix.metadata.attributes.find(x => x.trait_type === 'zorb').value;

    return (<div className="player-animation">
              <div className="relative">
                <ShaderSquare audioPlayer={audioPlayer} coord={coord}/>
                <Zorb style={{left: coord.x, top: coord.y}} className="player-zorb absolute z-30 w-8 h-8" zorbId={zorbId}/>
              <svg className="animation-background absolute z-1 top-0 left-0">
                <defs>
                  <filter id='shadow' color-interpolation-filters="sRGB">
                    <feDropShadow dx="2" dy="1" stdDeviation="2" flood-opacity="0.25"/>),
                    
                    
                  </filter>
                 <linearGradient id="linear-gradient" x1={coord.x +"%"} y1={coord.y + '%'} x2="75%" y2="0%">
                    <stop offset="-20%" stop-color="#3535354d"/>
                    <stop offset="50%" stop-color="#6363632e"/>
                    <stop offset="100%" stop-color="#2727279f"/>
                  </linearGradient>
                </defs>
                
                <path filter="url(#shadow)" d="M70 20C70 8.95431 61.0457 0 50 0C38.9543 0 30 8.9543 30 20C30 20 31.0457 30 20 30C8.95431 30 0 38.9543 0 50C0 61.0457 8.9543 70 20 70C31.0457 70 30 80 30 80C30 91.0457 38.9543 100 50 100C61.0457 100 70 91.0457 70 80C70 80 68.9543 70 80 70C91.0457 70 100 61.0457 100 50C100 38.9543 91.0457 30 80 30C68.9543 30 70 20 70 20Z" fill="white"/>
                <path filter="url(#shadow)" d="M69.4992 80.0145L69.5 80.0231C69.4875 90.782 60.7618 99.5 50 99.5C39.2381 99.5 30.5125 90.782 30.5 80.0231L30.5008 80.0145C30.5027 79.9926 30.5052 79.9614 30.5079 79.9215C30.5133 79.8418 30.5195 79.7273 30.5232 79.5831C30.5306 79.295 30.5281 78.8866 30.4892 78.3979C30.4117 77.4232 30.1884 76.1132 29.5985 74.7957C29.0067 73.474 28.0441 72.1413 26.4932 71.1422C24.9425 70.1432 22.8384 69.5 20 69.5C9.23045 69.5 0.5 60.7696 0.5 50C0.5 39.2304 9.23045 30.5 20 30.5C22.8384 30.5 24.9425 29.8568 26.4932 28.8578C28.0441 27.8587 29.0067 26.526 29.5985 25.2043C30.1884 23.8868 30.4117 22.5768 30.4892 21.6021C30.5281 21.1134 30.5306 20.705 30.5232 20.4169C30.5195 20.2727 30.5133 20.1582 30.5079 20.0785C30.5052 20.0386 30.5027 20.0074 30.5008 19.9855L30.5 19.9769C30.5125 9.21797 39.2381 0.5 50 0.5C60.7618 0.5 69.4875 9.21797 69.5 19.9769L69.4992 19.9855C69.4973 20.0074 69.4948 20.0386 69.4921 20.0785C69.4867 20.1582 69.4805 20.2727 69.4768 20.4169C69.4694 20.705 69.4719 21.1134 69.5108 21.6021C69.5883 22.5768 69.8116 23.8868 70.4015 25.2043C70.9933 26.526 71.9559 27.8587 73.5068 28.8578C75.0575 29.8568 77.1616 30.5 80 30.5C90.7695 30.5 99.5 39.2304 99.5 50C99.5 60.7696 90.7695 69.5 80 69.5C77.1616 69.5 75.0575 70.1432 73.5068 71.1422C71.9559 72.1413 70.9933 73.474 70.4015 74.7957C69.8116 76.1132 69.5883 77.4232 69.5108 78.3979C69.4719 78.8866 69.4694 79.295 69.4768 79.5831C69.4805 79.7273 69.4867 79.8418 69.4921 79.9215C69.4932 79.9375 69.4942 79.9521 69.4952 79.9653C69.4967 79.985 69.4981 80.0014 69.4992 80.0145Z" stroke="black" stroke-opacity="0.6"/>
                <path filter="url(#shadow)" d="M69.4992 80.0145L69.5 80.0231C69.4875 90.782 60.7618 99.5 50 99.5C39.2381 99.5 30.5125 90.782 30.5 80.0231L30.5008 80.0145C30.5027 79.9926 30.5052 79.9614 30.5079 79.9215C30.5133 79.8418 30.5195 79.7273 30.5232 79.5831C30.5306 79.295 30.5281 78.8866 30.4892 78.3979C30.4117 77.4232 30.1884 76.1132 29.5985 74.7957C29.0067 73.474 28.0441 72.1413 26.4932 71.1422C24.9425 70.1432 22.8384 69.5 20 69.5C9.23045 69.5 0.5 60.7696 0.5 50C0.5 39.2304 9.23045 30.5 20 30.5C22.8384 30.5 24.9425 29.8568 26.4932 28.8578C28.0441 27.8587 29.0067 26.526 29.5985 25.2043C30.1884 23.8868 30.4117 22.5768 30.4892 21.6021C30.5281 21.1134 30.5306 20.705 30.5232 20.4169C30.5195 20.2727 30.5133 20.1582 30.5079 20.0785C30.5052 20.0386 30.5027 20.0074 30.5008 19.9855L30.5 19.9769C30.5125 9.21797 39.2381 0.5 50 0.5C60.7618 0.5 69.4875 9.21797 69.5 19.9769L69.4992 19.9855C69.4973 20.0074 69.4948 20.0386 69.4921 20.0785C69.4867 20.1582 69.4805 20.2727 69.4768 20.4169C69.4694 20.705 69.4719 21.1134 69.5108 21.6021C69.5883 22.5768 69.8116 23.8868 70.4015 25.2043C70.9933 26.526 71.9559 27.8587 73.5068 28.8578C75.0575 29.8568 77.1616 30.5 80 30.5C90.7695 30.5 99.5 39.2304 99.5 50C99.5 60.7696 90.7695 69.5 80 69.5C77.1616 69.5 75.0575 70.1432 73.5068 71.1422C71.9559 72.1413 70.9933 73.474 70.4015 74.7957C69.8116 76.1132 69.5883 77.4232 69.5108 78.3979C69.4719 78.8866 69.4694 79.295 69.4768 79.5831C69.4805 79.7273 69.4867 79.8418 69.4921 79.9215C69.4932 79.9375 69.4942 79.9521 69.4952 79.9653C69.4967 79.985 69.4981 80.0014 69.4992 80.0145Z" stroke="black" stroke-opacity="0.6"/>
                <path filter="url(#shadow)" d="M69.4992 80.0145L69.5 80.0231C69.4875 90.782 60.7618 99.5 50 99.5C39.2381 99.5 30.5125 90.782 30.5 80.0231L30.5008 80.0145C30.5027 79.9926 30.5052 79.9614 30.5079 79.9215C30.5133 79.8418 30.5195 79.7273 30.5232 79.5831C30.5306 79.295 30.5281 78.8866 30.4892 78.3979C30.4117 77.4232 30.1884 76.1132 29.5985 74.7957C29.0067 73.474 28.0441 72.1413 26.4932 71.1422C24.9425 70.1432 22.8384 69.5 20 69.5C9.23045 69.5 0.5 60.7696 0.5 50C0.5 39.2304 9.23045 30.5 20 30.5C22.8384 30.5 24.9425 29.8568 26.4932 28.8578C28.0441 27.8587 29.0067 26.526 29.5985 25.2043C30.1884 23.8868 30.4117 22.5768 30.4892 21.6021C30.5281 21.1134 30.5306 20.705 30.5232 20.4169C30.5195 20.2727 30.5133 20.1582 30.5079 20.0785C30.5052 20.0386 30.5027 20.0074 30.5008 19.9855L30.5 19.9769C30.5125 9.21797 39.2381 0.5 50 0.5C60.7618 0.5 69.4875 9.21797 69.5 19.9769L69.4992 19.9855C69.4973 20.0074 69.4948 20.0386 69.4921 20.0785C69.4867 20.1582 69.4805 20.2727 69.4768 20.4169C69.4694 20.705 69.4719 21.1134 69.5108 21.6021C69.5883 22.5768 69.8116 23.8868 70.4015 25.2043C70.9933 26.526 71.9559 27.8587 73.5068 28.8578C75.0575 29.8568 77.1616 30.5 80 30.5C90.7695 30.5 99.5 39.2304 99.5 50C99.5 60.7696 90.7695 69.5 80 69.5C77.1616 69.5 75.0575 70.1432 73.5068 71.1422C71.9559 72.1413 70.9933 73.474 70.4015 74.7957C69.8116 76.1132 69.5883 77.4232 69.5108 78.3979C69.4719 78.8866 69.4694 79.295 69.4768 79.5831C69.4805 79.7273 69.4867 79.8418 69.4921 79.9215C69.4932 79.9375 69.4942 79.9521 69.4952 79.9653C69.4967 79.985 69.4981 80.0014 69.4992 80.0145Z" stroke="black" stroke-opacity="0.6"/>
 
                {synthOptions.map((stemId, i) =>
                    <circle className="synth-option" cx={39 + 11*i} cy={i===1 ? 12 : 20} r="2" fill={synths === stemId ? color("0xbeef8b1a18b376a82d87d1797359a7adf09f3dcc " + stemId) : "#54545494"}/>)}
                {drumOptions.map((stemId, i) =>
                    <circle cx={39 + 11*i} cy={i === 1 ? 88 : 80} r="2" fill={drums === stemId ? color("0xbeef8b1a18b376a82d87d1797359a7adf09f3dcc " + stemId) : "#54545494"}/>)}
                <rect x="30" y="30" width="40" height="40"  rx="15" fill="#16161682"/>
              </svg>
            {/*<img className="player-outline absolute z-1 top-0 left-0" src="/player-outline.svg"/>*/}
              </div>
            </div>
           );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixAnimation);
