export const fragment = `

uniform float x;
uniform float y;
uniform float width;
uniform float height;
uniform float time;
uniform sampler2D leftChannel;
uniform sampler2D rightChannel;
uniform sampler2D imageTexture;

float piecewise(sampler2D iChannel0, float x, float res) {
    float xTimesRes = x * res;

    // Left sample point:
    float x1 = floor(xTimesRes) / res;
    float y1 = texture(iChannel0, vec2(x1, 0.0)).x;

    // Right sample point:
    float x2 = ceil(xTimesRes) / res;
    float y2 = texture(iChannel0, vec2(x2, 0.0)).x;

    // Prevent small breaks in the line:
    x2 += 0.001;    

    // Fit half of a sine wave between sample points:
    float sine = sin(((x - x1) / (x2 - x1) * 2.0 - 1.0) * 1.5707963267);
    return y1 + (0.5 + 0.5 * sine) * (y2 - y1);
}

vec2 getOscPoint(float x) {

   float alignment = 0.9375;
   float returnTuning = .224399476;

   float left = x * alignment;
   float right = fract(x - returnTuning / alignment) * alignment;
    
   float pointX = piecewise(leftChannel,left, 20.0);
   float pointY = piecewise(rightChannel,right, 20.0);

   return vec2(pointX, pointY);
}

float getStereo(vec2 uv) {
    int samples = 500;
    float oneOverN = 1.0 / float(samples);
    
    float posOffset = 0.0;
    float timeOffset = 0.0;
    float xOffset = fract(posOffset + timeOffset) * oneOverN;
    float lineWidth = 0.006;
    
    float acc = 0.0;
    for (int i = 0; i < samples; i++) {
        float x = float(i) * oneOverN + xOffset;
        vec2 point = getOscPoint(x);

        vec2 pa = point - uv;
        float d = dot(pa, pa);
        
        acc += 1.0 / (d + lineWidth*lineWidth); // Inverse square law
    }
	
    return acc * oneOverN * 3.01 * lineWidth;
}


void main() {
  vec2 uv = (1.0*gl_FragCoord.xy-vec2(width, height))/height + vec2(1.0, 1.0);

  float o = getStereo(uv*.5);
  gl_FragColor = vec4(o, 0.0,0.0,o);
  //gl_FragColor = vec4(texture2D(rightChannel, vec2(gl_FragCoord.x/width, gl_FragCoord.y/height)).xy, 0.0, 1.0);
}
`;
