import React, {useState, useRef, useEffect, useCallback} from 'react';
import {useInterval} from 'components/hooks/useInterval.js';
import AudioUnit from 'engine/audio.js';
import Tone from 'tone';

const DELAY = 30;

export const useWaveformPlayer = ({buffer, start, length}) => {
    const loaded = useRef(false);
    const startRef = useRef(start);
    const lengthRef = useRef(length);
    const [isPlaying, setIsPlaying] = useState(false);
    const [player, setPlayer]  = useState(new AudioUnit(Tone.Master.context));

    useEffect(() => {
        startRef.current = start;
        lengthRef.current = length;
        setIsPlaying(false);
        player.stopSound();
    }, [start, length, setIsPlaying]);

    useEffect(() => {
        if (!buffer) {
            return;
        }
   }, [buffer]);

    const playSound = useCallback(() => {
        if (!loaded.current) {
            let audioBuffer = new AudioBuffer({length: buffer[0].length, sampleRate: 44100, numberOfChannels: buffer.length});
            audioBuffer.copyToChannel(buffer[0], 0);
            if (buffer[1]) {
                audioBuffer.copyToChannel(buffer[1], 1);
            }
            player.setBuffer(audioBuffer);
            loaded.current = true;
        }
        player.playSound(startRef.current/100*player.duration, lengthRef.current/100*player.duration);
        //setPlayhead(startRef.current);
    }, []);

    const play = useCallback(() => {
        if (isPlaying) {
            player.stopSound();
        } else {
            playSound();
        }
        setIsPlaying(!isPlaying);
    }, [isPlaying, setIsPlaying, start, length]);

    return {player, setIsPlaying, startRef, lengthRef, play, isPlaying, stop: () => player.stopSound()};
};
