import React, { useEffect, useCallback, useState} from 'react';
import SlicingTooltip from './SlicingTooltip.js';
import {connect} from 'react-redux';

const SliceControl = ({
    bpm,
    setBPM,
    length,
    detune,
    setDetune,
    beatGuide,
    endTime,
    start,
    buffer,
    width,
    dragStartControl,
    dragEndControl
}) => { 
    return (
        <div
          style={{left: 30+start/100*(width*2)+'px', width: length/100*(width*2)+"px"}}
          className="slicer">
          <div
            onMouseDown={dragStartControl}
            onTouchStart={dragStartControl}
            className="start-control">
            <SlicingTooltip
              bpm={bpm}
              setBPM={setBPM}
              length={length}
              detune={detune}
              setDetune={setDetune}
              beatGuide={beatGuide}
              endTime={endTime}
              start={start}
              buffer={buffer}/>
            <div className="arrow-left"/>
          </div>
          <div
            onTouchStart={dragEndControl}
            onMouseDown={dragEndControl}
            className="end-control">
            <div className="arrow-right"/>
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
      start: state.spore.slicePoints.start,
      length: state.spore.slicePoints.length,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceControl);
