import SavingProgressInfo from './SavingProgressInfo.js';
import PlayButtonAndPlayhead from './PlayButtonAndPlayhead.js';
import SliceControl from './SliceControl.js';
import SliceToolbar from './SliceToolbar.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {
    setSlicePoints,
    setSliceLength} from 'actions/index.js';
import { useAccount } from 'wagmi';
import {ethers} from 'ethers';
import {useZporeMintPrice} from './useZporeMintPrice.js';
import {useCroppedStemsData} from './useCroppedStemsData.js';
import {newZpore} from 'zpore/zpore.js';
//import TextHelper from './TextHelper.js';
import {writeScreenshot} from './screenshot.js';
import {usePostRemix} from './usePostRemix.js';
import SlicingTooltip from './SlicingTooltip.js';
import {REMIX_COLLECTION_ADDRESS, REMIX_TOKEN_ID} from 'constants/zpore-addresses.js';
import {useMagneticSlicer} from './useMagneticSlicer.js';
import React, { useEffect, useCallback, useRef, useState} from 'react';
import ipfs from 'decentralized/Ipfs.js';
import {cropAndStore} from 'utils/cropAndStore.js';
import {newSpore} from 'spores/newSpore.js';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import { useSigner } from 'wagmi';
import {isMobile} from 'react-device-detect';
import {connect} from 'react-redux';
import SliceWaveformSVG from './SliceWaveformSVG.js';
import Captioner from './Captioner.js';
import './slice-waveform.scss';

const SliceWaveform = ({
    store,
    silence,
    songId,
    endTime,
    screenshot,
    zorbId,
    beatGuide,
    waveform,
    setSlicePoints,
    setSliceLength,
    buffer,
    onCancel,
    zorbMovements,
    remixesHistory,
    startStep,
    stemsData, // these 2 should be 1...
    loopsData, 
}) => { 
    const {data: account, isError, isLoading} = useAccount();
    let ref = useRef();
    const {data: signer} = useSigner();
    let [saving, setSaving] = useState();
    let [caption, setCaption] = useState('');
    let [isWaitingForSignature, setIsWaitingForSignature] = useState();
    let [downloading, setDownloading] = useState(false);
    let [isCaptioning, setIsCaptioning] = useState(false);
    let [width, setWidth] = useState();
    let [download, setDownload] = useState();
    let [transaction, setTransaction] = useState();
    let [bpm, setBPM] = useState(120);
    let [detune, setDetune] = useState(0);

    useEffect(() => {
        return () => setSlicePoints(0, 100);
    }, []);



    useEffect(() => {
        // calculates the width that should be set for the waveform
        let _width = isMobile ? 300 : ref.current.offsetWidth;
        let width = (_width - 60)/2;
        setWidth(width);
    }, [setWidth]);

    const {
        dragEndControl,
        dragStartControl,
        draggingStart,
        draggingEnd
    } = useMagneticSlicer({
        store,
        setLength: setSliceLength,
        setSlicePoints,
        ref, width, buffer, startStep, silence, waveform, endTime, beatGuide, endTime});


    let {dropAddress, mintPrice} = useZporeMintPrice(songId);


    if (transaction || downloading || isWaitingForSignature) {
        return (
            <div className="slicer-backdrop">
              <SavingProgressInfo
                hide={(x) => onCancel(true)}
                download={download}
                transaction={transaction}
                downloading={downloading}
                transaction={transaction}
                isWaitingForSignature={isWaitingForSignature}/>
            </div>
        );
    }

    return (
        <div className="slicer-backdrop">
          <div
            className="slice-waveform">
            <PlayButtonAndPlayhead buffer={buffer} width={width}/>
           <div
              ref={ref} 
              className="waveform-container">
              {
               width &&
               <SliceWaveformSVG
                 endTime={endTime}
                 beatGuide={beatGuide}
                 silence={silence}
                 height={isMobile ? 90 : 100}
                 buffer={buffer}
                 startStep={startStep}
                 waveform={waveform} width={width}/>
              }
              
              {!isCaptioning &&
               <SliceControl
                 bpm={bpm}
                 setBPM={setBPM}
                 detune={detune}
                 setDetune={setDetune}
                 beatGuide={beatGuide}
                 endTime={endTime}
                 buffer={buffer}
                 width={width}
                 dragStartControl={dragStartControl}
                 dragEndControl={dragEndControl}
                 />
              }
            </div>
             <div className="slice-guide-info">
               slice your remix to beam into other spores
             </div>
          </div>

          {downloading ? '' : download ?
           <div className="slice-toolbar">
             <div onClick={onCancel} className="done-btn pushable continue">
               Continue
             </div>
           </div> :
           <SliceToolbar
             draggingStart={draggingStart}
             draggingEnd={draggingEnd}
             isCaptioning={isCaptioning}
             onCancel={onCancel}
             account={account}
             setDownloading={setDownloading}
             setSaving={setSaving}
             saving={saving}
             setIsCaptioning={setIsCaptioning}
             bpm={bpm}
             detune={detune}
             mintPrice={mintPrice}
             screenshot={screenshot}
             songId={songId}
             caption={caption}
             buffer={buffer}
             signer={signer}
             setIsWaitingForSignature={setIsWaitingForSignature}
             setDownload={setDownload}
             stemsData={stemsData}
             setTransaction={setTransaction}
             zorbId={zorbId}
             stemsData={stemsData}
             loopsData={loopsData}
             startStep={startStep}
             silence={silence}
             endTime={endTime}
             remixesHistory={remixesHistory}
             zorbMovements={zorbMovements}
           />
}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSlicePoints: (a,b) =>dispatch(setSlicePoints(a,b)),
      setSliceLength: (a) =>dispatch(setSliceLength(a))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceWaveform);
