import React, { useEffect, useCallback, useState, useRef} from 'react';
import {textureFromList} from 'components/shaders/texture.js';
import {useShader} from 'components/shaders/useShader.js';
import {fragment} from './fragment.js';
import {connect} from 'react-redux';
import {useInterval} from 'components/hooks/useInterval.js';

const ShaderSquare = ({coord, audioPlayer}) => { 
    let ref = useRef();
    const canvasRef = useRef();
    const leftTexture = useRef(textureFromList(512));
    const leftChannel = useRef();
    const rightTexture = useRef(textureFromList(512));
    const [uniforms, setUniforms] = useState({
        x: coord.x,
        y: coord.y,
        leftChannel: leftTexture.current(new Array(512).fill(255), true),
        rightChannel: rightTexture.current(new Array(512).fill(255), true),
    });

    useEffect(() => {
        /*
        setUniforms({
            x: coord.x,
            y: coord.y,
        });
        */
    }, [coord,  setUniforms]);
    let {updateUniforms} = useShader({ref, canvasRef, width: 130, height: 130, uniforms, fragmentShader: fragment});

    const onTick = useCallback(() => {
        if (!audioPlayer) {
            return;
        }
        let [left, right] = [audioPlayer.leftWaveform.getValue(), audioPlayer.rightWaveform.getValue()];
        let _left = leftTexture.current(left, true);
        let _right= rightTexture.current(right, true);
        _left.needsUpdate = true;
        _right.needsUpdate = true;
        let _uni = {
            ... uniforms,
            leftChannel: _left,
            rightChannel: _right
        };
        updateUniforms(_uni);
        leftChannel.current = _left;
    }, [audioPlayer, setUniforms, uniforms]);

    useInterval(onTick, 20);

    return (
        <div
          ref={ref}
          className="shader-square absolute">
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

let counter=0;
const fake = () => {
    counter++;
    if (counter % 8 < 4) {
        return [new Array(512).fill(1), new Array(512).fill(1)];
    } else {
        return [new Array(512).fill(-.8), new Array(512).fill(-.8)];
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ShaderSquare);
