import * as THREE from "three";

export const textureFromList = (size) => {
    let data = new Uint8Array( 4 * size );
    for (let i=0; i < 4*size; i++) {
        data[i] = 255;
    }
    let texture;
    return (list, normalize) => {
        /*
        if (texture) {
            return texture;
        }
        let test = new Uint8Array(4*4);
        for (let i=0; i < 4*4; i++ ){
            test[i] = 255;
        }
        texture = new THREE.DataTexture(test, 4, 1);
        texture.magFilter = THREE.NearestFilter;
        return texture;
        */
        
        let _data = new Uint8Array( 4 * size );
        for ( let i = 0; i < size; i ++ ) {
            
	    const stride = i * 4;
            
            if (normalize) {
	        _data[ stride ] = (list[i] + 1)/2 * 255;
            } else {
	        _data[ stride ] = list[i];
            }
	    _data[ stride + 1 ] = 0.0;
	    _data[ stride + 2 ] = 0.0;
	    _data[ stride + 3 ] = 0.0;
        }
        data.set(_data, 0);
        
        // used the buffer to create a DataTexture
        
        if (!texture) {
            texture = new THREE.DataTexture( data, size, 1);
        }  
        texture.needsUpdate = true;
        return texture;
    }
};
