import React, { useEffect, useCallback, useState} from 'react';
import {useCroppedStemsData} from './useCroppedStemsData.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import {usePostRemix} from './usePostRemix.js';
import {connect} from 'react-redux';

const SliceToolbar = ({
    isCaptioning,
    onCancel,
    account,
    setDownloading,
    setSaving,
    saving,
    setIsCaptioning,
    bpm,
    detune,
    mintPrice,
    screenshot,
    songId,
    caption,
    buffer,
    signer,
    setIsWaitingForSignature,
    setDownload,
    stemsData,
    loopsData,
    startStep,
    silence,
    endTime,
    remixesHistory,
    zorbMovements,
    setTransaction,
    zorbId,
    start,
    length,
    draggingStart,
    draggingEnd
}) => { 

    const {
        croppedStemsData
    } = useCroppedStemsData({
        draggingStart,
        draggingEnd,
        stemsData, loopsData, start, length, startStep, silence, buffer, endTime, remixesHistory, zorbMovements});

    // todo: combine this data into one esy to use object...
    const post = usePostRemix({
        bpm,
        detune,
        mintPrice,
        stemsData: croppedStemsData,
        screenshot,
        songId,
        caption,
        buffer,
        signer,
        start,
        setIsWaitingForSignature,
        setDownloading,
        length,
        setDownload,
        stop: () => [],
        setTransaction,
        zorbId
    });

    return (<div className={isCaptioning ? "slice-toolbar captioning" : "slice-toolbar"}>
              <div onClick={isCaptioning ? () => setIsCaptioning(false) : onCancel} className="cancel-btn pushable">
                {isCaptioning ? "Edit" : "Cancel"}
              </div>
              {!account || !account.address ?
               <div className="final-connect"><ConnectButton
                                                accountStatus="address" 
                                                showBalance={{ smallScreen: false, largeScreen: false }}
                                                label="Connect Wallet"
                                              /> </div>: 
               
               <div onClick={() => {
                   setDownloading(true);
                   setTimeout(() => {
                       setSaving(true)
                       post();
                   }, 40);
               }} className={(saving ? "opacity-20 " : "") + "done-btn pushable"}>
                 Save
               </div>}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
      start: state.spore.slicePoints.start,
      length: state.spore.slicePoints.length,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceToolbar);
