import React, { useEffect, useCallback, useState} from 'react';
import {useInterval} from 'components/hooks/useInterval.js';
import {connect} from 'react-redux';

const DELAY = 30;

const Playhead = ({start, player, setIsPlaying, width, startRef, lengthRef, isPlaying}) => { 

    let [playhead, setPlayhead] = useState(0);
    const onTick = useCallback(() => {
        if (isPlaying) {
            let offset = 100* (DELAY/1000) / player.duration;
            if (playhead + offset > startRef.current + lengthRef.current) {
                setIsPlaying(false);
                setPlayhead(startRef.current);
            } else {
                setPlayhead(playhead + offset);
            }
        }
    }, [isPlaying, setPlayhead, playhead, player]);

    useEffect(() => {
        setPlayhead(start);
    }, [isPlaying, start]);

    useInterval(onTick, DELAY);

    return( 
        <div
          style={{left: 30 + (playhead/100)*(width)*2+'px'}}
          className="playhead"/>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Playhead);
