import * as THREE from "three";
import React, { useRef, useEffect, useCallback, useState} from 'react';
import {defaultVertexShader} from './vertex.js';

export const useShader = ({
    canvasRef, 
    ref,
    resolution=1,
    width,
    height,
    vertexShader=defaultVertexShader,
    fragmentShader,
    uniforms,
}) => {
    let start = useRef(new Date().getTime());
    let renderer = useRef();
    let scene = useRef();
    let camera = useRef();
    let shaderUniforms = useRef({});
    let shaderMaterial = useRef();

    useEffect(() => {
        init();
        animate();
    }, []);

    const init = () => {
	// Initialize the WebGL renderer
	renderer.current = new THREE.WebGLRenderer();
	renderer.current.setPixelRatio(2);
	renderer.current.setSize(width/resolution, height/resolution);
        renderer.current.setClearColor( 0x000000, 0);

        if (canvasRef) {
            canvasRef.current = renderer.current.domElement;
        }
        
	// Add the renderer to the sketch container
	ref.current.appendChild(renderer.current.domElement);
        
	// Initialize the scene
	scene.current = new THREE.Scene();
        
	// Initialize the camera
	camera.current = new THREE.Camera();
        
	// Create the plane geometry
        let geometry = new THREE.PlaneGeometry(2, 2);

	// Define the shader uniforms
	shaderUniforms.current = {
            ... shaderUniforms.current,
            x: {
                value: 20,
            },
            y: {
                value: 40,
            },
            width: {
                value: width / 2,
            },
            height: {
                value: height / 2
            },
	};

        _updateUniforms(shaderUniforms.current, uniforms);

	// Create the shader material
	var material = new THREE.ShaderMaterial({
	    uniforms : shaderUniforms.current,
	    vertexShader,
	    fragmentShader
	});

        shaderMaterial.current = material;

	// Create the mesh and add it to the scene
	var mesh = new THREE.Mesh(geometry, material);
	scene.current.add(mesh);
    };

    useEffect(() => {
        shaderUniforms.current.width.value = width;
        shaderUniforms.current.height.value = height;
	renderer.current.setSize(width/resolution, height/resolution);
    }, [width, height, resolution]);

    function animate() {
	//requestAnimationFrame(animate);
	//render();
    }

    const _updateUniforms = (output, uniforms) => {
        for (let key in uniforms) {
            if (key in output) {
                output[key].value = uniforms[key];
            } else {
                output[key] = {
                    value: uniforms[key],
                }
                if (isNaN(uniforms[key])) {
                    output[key].type = "t";
                }
            }
            //shaderMaterial.current.uniforms[key].needsUpdate = true;
            if (isNaN(uniforms[key])) {
                output[key].type = "t";
                output[key].value.needsUpdate = true;
            }
        }
    };
    useEffect(() => {
        for (let key in uniforms) {
            if (key in shaderMaterial.current.uniforms) {
                shaderMaterial.current.uniforms[key].value = uniforms[key];
                if (key === "leftChannel") {
                }
            } else {
                shaderMaterial.current.uniforms[key] = {
                    value: uniforms[key],
                }
                if (isNaN(uniforms[key])) {
                    shaderMaterial.current.uniforms[key].type = "t";
                }
            }
            //shaderMaterial.current.uniforms[key].needsUpdate = true;
            if (isNaN(uniforms[key])) {
                shaderMaterial.current.uniforms[key].type = "t";
                shaderMaterial.current.uniforms[key].value.needsUpdate = true;
            }
        }
        if (shaderMaterial.current) {
            // shaderMaterial.current.uniformsNeedUpdate = true;
            shaderMaterial.current.needsUpdate = true;
        }
        render();
    }, [uniforms]);
    
    /*
     * Renders the sketch
     */
    function render() {
        if (!renderer.current.domElement) {
            return;
        }
        shaderMaterial.current.uniforms["time"] = {
            value: (new Date().getTime() - start.current)/1000
        };
        shaderMaterial.current.needsUniformUpdate = true;
        shaderMaterial.current.needsUpdate = true;
        let key = "leftChannel";
        if (shaderMaterial.current.uniforms[key].type === "t") {
            shaderMaterial.current.uniforms[key].value.needsUpdate = true;
            let value = shaderMaterial.current.uniforms[key].value;
        }
	renderer.current.render(scene.current, camera.current);
    }

    const updateUniforms = useCallback((uniforms) => {
        _updateUniforms(shaderUniforms.current, uniforms);
        render();
    }, []);
    return {updateUniforms};
};
